* {
  padding: 0;
  margin: 0;
}
*,
::before,
::after {
  box-sizing: border-box;
}
body {
  font-family: 'Josefin Sans', sans-serif;
}
/* Landing */

.main {
  height: 100vh;
  background-color: #181824;
  position: relative;
  overflow: hidden;
  transition: background-color 0.3s;
}
.light {
  background-color: #fafafa;
}
.top-img {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.top-img > img {
  user-drag: none;
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  width: 100%;
}
.main > .content {
  width: 500px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 100px;
}
@media (max-width: 767px) {
  .main > .content {
    width: 400px;
    max-width: 100%;
  }
}
@media (max-width: 430px) {
  .main > .content {
    width: 300px;
    max-width: 100%;
  }
}

/* Start Header */

.main header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main header h1 {
  color: white;
  font-size: 30px;
  font-weight: normal;
  letter-spacing: 6px;
}
.main header > button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.main header > button > svg {
  color: rgb(189, 189, 189);
  font-size: 20px;
  transition: color 0.3s;
}
.main header > button > svg:hover {
  color: white;
}
/* Form */
.main .form {
  display: flex;
  background-color: #25273c;
  border-radius: 2px;
  height: 45px;
  margin-top: 25px;
  box-shadow: 5px 5px 5px #1708204d;
  transition: background-color 0.3s, box-shadow 0.3s;
}
.light .form {
  background-color: #ffffff;
  box-shadow: 5px 5px 5px #17082010;
}
.main .form > button {
  background-color: transparent;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  padding: 10px;
}
.main .form > button > svg {
  color: rgb(142, 142, 142);
  font-size: 20px;
  transition: color 0.3s;
}
.main .form > button > svg:hover {
  color: white;
}
.light .form > button > svg:hover {
  color: #9181f4;
}
.main .form > input {
  width: 100%;
  background-color: transparent;
  border: none;
  outline: none;
  color: white;
  transition: color 0.3s;
}
.light .form > input {
  color: black;
}
.main .form > input::placeholder {
  color: white;
}
.light .form > input::placeholder {
  color: rgba(0, 0, 0, 0.527);
}
/* Start Tasks */
.main .content .tasks-content {
  margin-top: 15px;
  background-color: #25273c;
  box-shadow: 5px 5px 5px #1708204d;
  transition: background-color 0.3s, box-shadow 0.3s;
}
.light .content .tasks-content {
  background-color: #ffffff;
  box-shadow: 5px 5px 5px #17082010;
}
.main .content .tasks-content .tasks {
  display: flex;
  flex-direction: column;
}
.main .content .tasks-content .tasks .task {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 45px;
  border-bottom: 1px solid rgb(106, 106, 106);
}
.light .content .tasks-content .tasks .task {
  border-bottom: 1px solid rgba(106, 106, 106, 0.245);
}

.main .content .tasks-content .tasks .task > button {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 10px;
}

.main .content .tasks-content .tasks .task > button > svg {
  color: rgb(142, 142, 142);
  font-size: 20px;
  transition: color 0.3s;
}
.main .content .tasks-content .tasks .task > .close svg {
  opacity: 0;
  transition: opacity 0.3s;
}
.main .content .tasks-content .tasks .task:hover .close svg {
  opacity: 1;
}
.main .content .tasks-content .tasks .task > .check > svg:hover {
  color: #9181f4;
}
.main .content .tasks-content .tasks .task > .close > svg:hover {
  color: white;
}
.light .content .tasks-content .tasks .task > .close > svg:hover {
  color: #9181f4;
}
.main .content .tasks-content .tasks .task > p {
  color: rgb(209, 209, 209);
  font-weight: 300;
  flex: 1;
  font-size: 15px;
  cursor: pointer;
  transition: color 0.3s;
}
.light .content .tasks-content .tasks .task > p {
  color: rgb(54, 54, 54);
}
.main .content .tasks-content .tasks .task > p:hover {
  color: white;
}
.light .content .tasks-content .tasks .task > p:hover {
  color: rgb(0, 0, 0);
}
/* Footer */
.main .content .tasks-content footer {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}
.main .content .tasks-content footer .items-left {
  color: #63647c;
  font-size: 14px;
}
@media (max-width: 430px) {
  .main .content .tasks-content footer .items-left {
    font-size: 11px;
  }
}
.main .content .tasks-content footer button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: #63647c;
  transition: color 0.3s;
  font-weight: 500;
  font-size: 14px;
}
@media (max-width: 430px) {
  .main .content .tasks-content footer button {
    font-size: 11px;
  }
}
.main .content .tasks-content footer button:hover {
  color: #9181f4;
}
.main .content .tasks-content footer > nav {
  display: flex;
  gap: 8px;
}
